#player_ctn {
	position: fixed;
	aspect-ratio: 16/9;
	top: 0%;
	height: auto;
	width: 100svw;
	min-height: 1000px;
	overflow: hidden;
	display: flex;
	justify-content: center;
}

#player_ctn::after {
	content: "";
	background-color: rgb(0, 0, 0); /* Adjust opacity here */
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100%;
	opacity: 1;
	animation: fadeIn ease-in 2500ms forwards;
	animation-delay: 1000ms;
}

.video_ctn {
	position: absolute;
	width: auto; /* Maintain original width */
	object-fit: cover;
	min-width: 1900px;
	min-height: 1000px;
}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 0.5;
	}
}
